import React, { useRef, useCallback, useState, useEffect } from "react";
import "./styles/App.scss";
import {
  BrowserRouter as Router,
  Link,
  useLocation,
  useHistory,
} from "react-router-dom";
import { DATA } from "./DATA.js";

const useQuery = () => {
  return new URLSearchParams(useLocation().search);
};

const App = () => {
  const [selectedProducts, setSelectedProducts] = useState({
    product_1: 0,
    product_2: 0,
    product_3: 0,
    product_4: 0,
    product_5: 0,
    product_6: 0,
    product_7: 0,
    product_8: 0,
    product_9: 0,
    product_10: 0,
    product_11: 0,
    product_12: 0,
    product_13: 0,
    product_14: 0,
  });
  const handleChange = ({ target: { value } }) => {
    setSelectedProducts((prev) => ({ ...prev, product_1: value }));
  };
  return (
    <>
      <header>
        <img
          className="logo"
          src={require("./assets/logo.png")}
          alt="none"
        ></img>
        <h1>장례 규모에 맞춘 자유선택형 상조</h1>
        <h5>
          본 서비스는 장례준비가 되지 않은 상황에서도 장례식 및 상조비용을
          사전에 산출하여 규모에 맞는 용품과 서비스를 합리적으로 사용하실 수
          있도록 개발되었습니다.
        </h5>
      </header>
      <main>
        {DATA.map((d) => (
          <Product
            no={d.no}
            category={d.category}
            products={d.products}
            explain={d.explain}
            selectedProducts={selectedProducts}
            handleChange={handleChange}
          />
        ))}
      </main>
    </>
  );
};

const Product = ({
  no,
  category,
  products,
  explain,
  selectedProducts,
  handleChange,
}) => {
  return (
    <>
      <section className="product">
        <div className="subtitle">
          <div className="number">{no}</div>
          <h2>{category}</h2>
        </div>
        <div className="content">
          <img
            className="picture"
            src={require("./assets/picture_1.jpg")}
            alt="none"
          ></img>
          <div className="explain">
            <div className="explain__top">
              <h4 className="bold">제공내역</h4>
              <h3>{products[0].product}</h3>
            </div>
            <div className="explain__bottom">
              <h4>{explain}</h4>
              <select
                name="product_1"
                id="product_1"
                onChange={handleChange}
                value={selectedProducts.product_1}
              >
                <option value={0}>선택하세요*필수</option>
                {products.map((p, i) => (
                  <>
                    <option value={i + 1}>{p.product}</option>
                  </>
                ))}
              </select>
            </div>
          </div>
        </div>
      </section>
    </>
  );
};

export default App;
